import React from 'react';
import portrait from '../img/portrait.jpg';
import {PageInfoText} from './hoc/page_info_text';
import Navigation from '../navigation/navigation';
import Footer from './hoc/footer';

const Job = ({ number, company, position, details }) => {
    return (
      <>
        <div class={`job ${number}`}>
          <h3>{company}</h3>
          <h6>{position}</h6>
          <p>{details}</p>
        </div>
      </>
    );
  };

export default function AboutPage(){
    return(
        <div className="about-container">
        <Navigation about="current" />
        <main id="about">
          <PageInfoText
            Big1="About"
            Big2="Me"
            small="Let me tell you a few things..."
          />
          <div class="about-info">
            <img src={portrait} alt="Fiifi Arkhurst" class="bio-image" />
  
            <div class="bio">
              <h3 class="text-secondary">BIO</h3>
              <p>
               I'm Fiifi Foh Arkhurst Jr, a third year computer science student in University Of Ghana, Legon.
               A writer, tech enthusiast, software developer who has extensive knowledge in Internet 
               of Things (IoT) and network design. I am abreast with emerging technologies, trends and mainly focused React/React Native 
               framework for web and mobile development respectively. I see coding as a form of expression, like how artists express what they feel using to paintbrush. I like to use it 
               create experiences that help others express themselves too.
              </p>
              <p>
                With a strong passion for writing, I co-run a tech blog to keep myself up-to-date. I'm a team
                player who has been working out of his comfort zone to get things done. Still learning, get in touch let's
                solve some real world problems.
              </p>
            </div>
  
            <Job
              number="job-1"
              company="Dejays Inc"
              position="Co-founder || Writer"
              details="Collaborating with my team of idealists and teachnocrats, I write about Crypto, IoTs and trending innovations. @dejaysblog we also provide simple tutorials on tasks that need to be completed with your computers and mobile devices."
            />
            <Job
              number="job-2"
              company="Afrilogic Solutions"
              position="Full Stack Developer (Intern)"
              details="We utilize creating and innovative tools to provide top notch world-class IT solutions to clients. Using agile methodologies in most areas of application developement, we use NodeJs, C#, JavaScript & Microsoft Azure Cloud.  "
            />
            <Job
              number="job-3"
              company="Accra.Net & Power Platform Community"
              position="Volunteer"
              details="With inspirational and hardworking community leads on the Microsoft Stack (Microsoft Azure, Power Platform and 365 productivity), I contribute to help run organized meet-ups aimed promoting Africans in the industry. "
            />
            {/* <Job
              number="job-4"
              company="123 WebShop"
              position="Full Stack Developer"
              details="Lorem ipsum dolor sit amet consectetur adipisicing elit.
                Voluptates perferendis totam enim. Nesciunt porro dolores expedita
                dolor necessitatibus deserunt nemo."
            /> */}
          </div>
        </main>
        <Footer />
      </div>
    )
}