import React,{ useState} from 'react';
import { Link } from 'react-router-dom'

const List = ({ link, route, classNames }) => {
    return (
      <>
        <li className={classNames}>
          <Link to={route}>
            <span className="nav-link">{link}</span>
          </Link>
        </li>
      </>
    );
  };

  
const MenuBtn = () => {
    return (
      <>
        <div className="btn-line"></div>
        <div className="btn-line"></div>
        <div className="btn-line"></div>
      </>
    );
  };
  const showClass = "show";
  const closeClass = "close";

  export default function Navigation(props){

    const [ showMenu, setShowMenu ] = useState(false)

    const toggleMenu = () => {
        if(!showMenu){
            setShowMenu(true)
        }else{
            setShowMenu(false)
        }
    }
      return(
        <header className="navigation-container">
        <div
          className={`menu-btn ${!showMenu ? "" : closeClass} `}
          onClick={toggleMenu}
        >
          <MenuBtn />
        </div>
        <nav className={`menu ${!showMenu ? "" : showClass}`}>
          <div
            className={`menu-branding ${!showMenu ? "" : showClass}`}
          >
            <div className="portrait"></div>
          </div>
          <ul class={`menu-nav ${!showMenu ? "" : showClass}`}>
            <List
              route="/"
              link="Home"
              classNames={`nav-item ${!showMenu ? "" : showClass} ${
                props.home
              }`}
            />
            <List
              route="/about"
              link="About"
              classNames={`nav-item ${!showMenu ? "" : showClass} ${
                props.about
              }`}
            />
            <List
              route="/work"
              link="Work"
              classNames={`nav-item ${!showMenu ? "" : showClass} ${
                props.work
              }`}
            />
            <List
              route="/contact"
              link="How To Reach Me"
              classNames={`nav-item ${!showMenu ? "" : showClass} ${
                props.contact
              }`}
            />
           
          </ul>
        </nav>
      </header>
      )
  }