import React from 'react';
import { PageInfoText } from './hoc/page_info_text';
import Navigation from '../navigation/navigation';
import Footer from './hoc/footer';

export default function ContactPage(){
    return(
        <div className="contact-container">
        <Navigation contact="current" />
        <main id="contact">
          <PageInfoText
            Big1="Contact"
            Big2="Me"
            small="This is how you can reach me..."
          />
          <div className="boxes">
            <div>
              <span className="text-secondary">Email: </span> fiifi@dejaysblog.com
            </div>
            <div>
              <span className="text-secondary">Phone: </span> (+ 233) 024-2379278
            </div>
            <div>
              <span className="text-secondary">Address: </span> 50 Main st Boston
              MA 02101
            </div>
          </div>
        </main>
  
        <Footer />
      </div>
    )
}