import React from 'react';
import { PageInfoText } from './hoc/page_info_text';
import Navigation from '../navigation/navigation';
const SocialLink = ({ name, link }) => {
    return (
      <a href={link}>
        <i className={`fab ${name}`}></i>
      </a>
    ); 
  };


export default function HomePage(){
    return(
        <div className="home-container">
        <Navigation home="current" />
        <main id="home">
          <PageInfoText
            Big1="Fiifi"
            Big2=" Arkhurst Jr."
            small=" Software Developer, Tech Advocate, Writer & Entrepreneur"
          />
          <div class="icons">
           <SocialLink  name=" fa-twitter fa-2x" link="https://twitter.com/FiifiArkhurst" />
           <SocialLink name=" fa-linkedin fa-2x" link="http://linkedin.com/in/fiifi-foh-arkhurst-jnr-312085182" />
           <SocialLink name=" fa-github fa-2x" link="https://github.com/arkhurst" />
          </div>
          <br />
        </main>
      </div>
    )
}